  import urls from '../../../utils/urls.js'
  export default {
    data () {
      return {
        isCommit: false,
        isSure: false,
        second: 60,
        showText: true,
        phone: '',
        code: '',
				isLogin: this.$store.state.isLogin
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.userInfo
      }
    },
    methods: {
      // getPhone: function (e) {
      //   this.phone = e.detail.value
      // },
      getIdenCode: function (e) {
        if (e.detail.value.length >= 4) {
          this.isSure = true
          this.showText = true
        } else {
          uni.showToast({
            title: '请填写正确的验证码',
            icon: 'none'
          })
        }
      },
      getCode: function () {
        // var reg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
		var reg = /^1[345678]\d{9}$/
        var phone = this.phone
        if (!reg.test(phone)) {
          uni.showToast({
            title: '请输入正确的手机号',
            icon: 'none'
          })
        } else {          
          uni.request({
            url: urls.HTTP + '/user/getmobilecode',
						method: 'POST',
            data: {
              mobile: phone
            },
			success:(res) =>{
				if (res.data.code == 1) {
				  uni.showToast({
					title:res.data.error,
						icon:'none'
				  })
				}else{
					this.showText = false
					var interval = setInterval(() => {
					  --this.second
					}, 1000)
					setTimeout(() => {
					  clearInterval(interval)
					  this.showText = true
					}, 60000)
				}
			}
          })		  
        }
      },
      submit: function (e) {
        // var id = uni.getStorageSync('user').id
				// let id = JSON.parse(uni.getStorageSync('userInfo')).id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
        uni.request({
          url: urls.HTTP + '/user/editmobile',
          method: 'POST',
          data: {
            id: id,
            mobile:this.phone,
            code: e.detail.value.code
          },
          success: (res) => {
            console.log('修改手机号', res)
            if (res.data.code === 0) {
              uni.showModal({
                title: '提示',
                content: '修改成功！',
                showCancel: false,
                success: (res) => {
                  uni.redirectTo({
                    url: 'userInfo'
                  })
                }
              })
            }
          }
        })
      }
    }
  }
